import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.scss']
})
export class MenuComponent implements OnInit {
  
  items = [];

  constructor() {
    this.items=[
      {
        name: 'menu.home',
        icon: 'fa-home'
      },
      {
        name: 'menu.about',
        icon: 'fa-user',
        url: '/about'
      },
      {
        name: 'menu.academics',
        icon: 'fa-graduation-cap',
        url: '/academics'
      },
      {
        name: 'picture'
      },
      {
        name: 'menu.experience',
        icon: 'fa-flask'
      },
      {
        name: 'menu.blog',
        icon: 'fa-blog'
      },
      {
        name: 'menu.contact',
        icon: 'fa-envelope'
      }
    ]
  }

  ngOnInit() {
  }

}



